.catalog {
  padding: 8rem 0rem 15rem;

  @media (max-width: (991px)) {
    padding: 2rem 0rem;
  }
}

.catalog__block {
  padding: 5rem 0rem 5rem;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;

  @media (max-width: (768px)) {
    padding: 1rem 0rem 1rem;
  }
}

.catalog__block-item {
  margin-top: 4rem;

  @media (max-width: (768px)) {
    margin-top: 2rem;
  }
}

.catalog__block-top {
  cursor: pointer;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    background-color: #D8D8D8;
    opacity: 0;
    transition: opacity .2s linear;
  }

  &:hover {
    &::before {
      opacity: 30%;
    }
  }
}

.catalog__block-name {
  font-size: 2rem;
  color: $black;
  font-family: 'Raleway';
  font-weight: 500;
  text-align: center;

  @media (max-width: (768px)) {
    font-size: 1.5rem;
  }
}

.catalog__novelty {
  padding: 5rem 0rem 5rem;

  @media (max-width: (768px)) {
    padding: 1rem 0rem 1rem;
  }
}