.modal__panel {
  display: none;
  transition: background-color .2s linear;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 100;

  &--opened {
    display: block;
    background-color: rgba($color: #000000, $alpha: .3);
    overflow: hidden;
  }
}

.modal__container {
  display: none;
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 40%;
  background-color: #fff;
  padding: 5rem;
  transition: 2s;
  transform: translateX(100%);
  overflow: auto;

  &--opened {
    display: block;
    transform: translateX(0%);
  }

  @media (max-width: (768px)) {
    width: 70%;
  }

  @media (max-width: (499px)) {
    width: 100%;
  }
}

.modal__close {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  border: none;
  background: none;
  padding: 0;
  margin: 0;

  @media (max-width: (768px)) {
    position: relative;
    top: -15px;
    right: -15px;
  }
}

.modal__close-img {
  width: 16px;
  height: 16px;
  transition: transform .2s linear;
  cursor: pointer;

  &:hover {
    transform: rotate(15deg);
  }

  @media (max-width: (991px)) {
    width: 14px;
    height: 15px;
  }
  
  @media (max-width: (768px)) {
    width: 14px;
    height: 14px;
  }
}

.modal__title {
  font-size: 3rem;
  font-family: 'Raleway';
  font-weight: 500;
  color: $black;
  text-align: center;
  margin-bottom: 3rem;

  @media (max-width: (1199px)) {
    font-size: 2.5rem;
  }

  @media (max-width: (991px)) {
  font-size: 1.9rem;
  }

  @media (max-width: (768px)) {
  font-size: 1.5rem;
  }

  @media (max-width: (499px)) {
  font-size: 2rem;
  }
}

.modal__block {
  display: flex;
  justify-content: center;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
  align-items: center;
  width: 100%;
}

.modal__block-bottom {
  max-width: 380px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.modal__block-top {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  padding-top: 2rem;
}

.modal__block-text {
  max-width: 380px;
  font-size: 1.5rem;
  font-family: 'Inter';
  font-weight: 400;
  color: rgba(0, 0, 0, 0.3);
  margin-bottom: 3rem;

  @media (max-width: (991px)) {
    font-size: 1rem;
  }
}

.modal__product {
  display: flex;
  flex-direction: column;
  margin: 2rem 0rem;
}

.modal__product-info {
  display: flex;
  justify-content: space-between;
  border: 1px solid $black;
  margin-top: -.3rem;
  padding: 1.5rem;
}

.modal__product-name {
  padding: 0;
  margin: 0;
  font-size: 1.2rem;
  color: $black;
  font-family: 'Inter';
  font-weight: 400;

  @media (max-width: (768px)) {
    font-size: 1.1rem;
  }
}

.modal__product-price {
  margin: 0;
  padding: 0;
  font-size: 1.2rem;
  font-family: 'Inter';
  font-weight: 400;

  @media (max-width: (768px)) {
    font-size: 1.1rem;
  }
}