.swiper-slide {
  position: static;
}

.swiper-container {
  padding-bottom: 120px;
}

.swiper-button-prev,
.swiper-button-next {
  position: static;
  margin-left: 3.5rem;
  margin-top: 2rem;
  color: $black;
  background: none;
  border: none;
  outline: none;
  width: 8.75px;
  height: 17.5px;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  font-size: 1.8rem;
}
