.black__button,
.white__button {
  position: relative;
  display: flex;
  align-items: center;
  background-color: $black;
  border: none;
  font-size: 1.7rem;
  font-family: 'Inter';
  font-weight: 400;
  color: $white;
  padding: 1.8rem 2.4rem;
  cursor: pointer;
  transition: color .2s linear, transform .3s linear;

  &::after {
    content: "";
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    background-color: gray;
    bottom: 0;
    left: 0;
    opacity: 0;
    transition: opacity .2s linear;
  }

  &:hover {
    color: rgba(255, 255, 255, 0.8);
    transform: translateY(-2px);

    &::after {
      opacity: 1;
      transform: translateY(2px);
    }
  }

  &--active {
    color: rgba(255, 255, 255, 0.5);
  }

  &--call {
    width: 380px;
    justify-content: center;
    margin-top: 5rem;

    @media (max-width: (1199px)) {
      width: 100%;
    }
  }

  @media (max-width: (1199px)) {
    font-size: 1.5rem;
    padding: 1.6rem 2.2rem;
  }

  @media (max-width: (499px)) {
    justify-content: center;
    width: 100%;
  }
}

.white__button {
  background-color: $white;
  color: $black;

  &--active {
    color: rgba(0, 0, 0, 0.418);
  }

  &--footer {
    display: none;
    order: 3;
    margin-top: 4rem;

    @media (max-width: (768px)) {
      display: block;
    }
  }

  &:hover {
    color: rgba(0, 0, 0, 0.8);
    transform: translateY(-2px);

    &::after {
      opacity: 1;
      transform: translateY(2px);
    }
  }
}

.black__button-img {
  margin-left: 0.8rem;

  @media (max-width: (1199px)) {
    width: 10px;
    height: 8px;
  }
}

.up__button {
  font-size: 1.7rem;
  color: rgba(255, 255, 255, 0.4);
  font-family: 'Inter';
  border: none;
  background: none;
  cursor: pointer;
  position: absolute;
  bottom: 0;
  right: 0;

  &--footer {
    @media (max-width: (768px)) {
      display: none;
    }
  }
}